* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: 'Saira', sans-serif;
    font-size: 20px;
    line-height: 1.6;
}

input, button, select {
    font-family: 'Saira', sans-serif;
    font-size: 20px;
}

@media (max-width: 767px) {
    html, input, button, select {
        font-size: 16px;
    }
}

@media (max-width: 639px) {
    html, input, button, select {
        font-size: 14px;
    }
}
div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  max-width: 1600px;
  margin: 0 auto;
  text-align: center;
  background-color: #fafafa;
}

main {
  flex-grow: 1;
  margin: 0 auto;
  padding: 0 15px 80px;
}

main h2 {
  margin: 60px 0 40px;
  border-bottom: 2px solid #888;
  font-size: 40px;
}

main h3 {
  margin: 5px 0;
  font-size: 25px;
}

main button {
  display: block;
  margin: 30px auto 0px auto;
  padding: 5px 30px;
  background-color: white;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
  border: 2px solid #3d4145;
  border-radius: 12px;
  cursor: pointer;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

main button:hover {
  background-color: #b58502;
}

/* SECTION - CURRENCY CALCULATOR */

main section {
  display: flex;
  flex-direction: column;
}

main section h3 {
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  padding: 10px 20px;
  background-color: white;
}

/* TABLES WITH DOWNLOADED DATA */

main table {
  margin: 30px auto 0px auto;
  text-transform: uppercase;
  border-spacing: 0;
  background-color: white;
  padding: 20px;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
}

main table tr td {
  padding: 10px 30px;
  vertical-align: middle;
  text-align: left;
}

main table tr:not(:last-child) td {
  border-bottom: 1px solid #777;
}

main table tr:first-child td {
  text-align: center;
  font-weight: bold;
}

main table tr td:first-child, main table tr td:last-child {
  text-align: center;
}

main form {
  background-color: #d2e5fa;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  padding: 20px;
  margin: 30px 0;
  font-weight: bold;
  line-height: 2.5;
}

main form:first-child {
  margin-top: 60px;
}

@media (max-width: 1023px) {
  main h2 {
    font-size: 36px;
  }

  main h3 {
    font-size: 22px;
  }
}

@media (max-width: 767px) {
  main h2 {
    font-size: 32px;
  }

  main h3 {
    font-size: 20px;
  }

  main table {
    padding: 10px;
  }
}

@media (max-width: 639px) {
  main h2 {
    font-size: 28px;
  }

  main h3 {
    font-size: 16px;
  }

  main table tr td {
    padding: 10px 15px;
  }

  main table tr:nth-child(n) td {
    text-align: center;
  }
}

@media (max-width: 459px) {
  main {
    padding: 0 15px 60px;
  }

  main h2 {
    font-size: 24px;
  }

  main h3 {
    font-size: 14px;
  }

  main section form {
    margin-top: 10px;
  }

  main table tr td {
    padding: 10px;
  }
}

@media (max-width: 359px) {
  main table tr td {
    padding: 10px 5px;
  }
}
header {
    width: 100%;
    height: 250px;
    margin-top: 60px;
    background-image: url(/currency-calculator/static/media/bg1.03776ae3.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

header h1 {
    background-color: rgba(0, 0, 0, 0.6);
    font-family: 'Saira Condensed', sans-serif;
    color: white;
    font-size: 54px;
    text-align: center;
    line-height: 250px;
}

@media (max-width: 1279px) {
    header {
        height: 200px;
    }

    header h1 {
        font-size: 40px;
        line-height: 200px;
    }
}

@media (max-width: 1023px) {
    header {
        height: 150px;
        margin-top: 56px;
    }

    header h1 {
        font-size: 32px;
        line-height: 150px;
    }
}

@media (max-width: 767px) {
    header {
        height: auto;
        margin-top: 54px;
    }

    header h1 {
        padding: 50px 20px;
        font-size: 28px;
        line-height: 2;
    }
}

@media (max-width: 459px) {
    header h1 {
        padding: 20px;
        font-size: 24px;
        line-height: 1.8;
    }
}

@media (max-width: 359px) {
    header h1 {
        padding: 10px;
        font-size: 22px;
    }
}
nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 1600px;
    height: 60px;
    padding: 0 50px;
    background-color: white;
    border-bottom: 2px solid #ccc;
}

nav ul {
    display: flex;
    list-style-type: none;
}

nav ul li {
    flex-basis: 30%;
    flex-grow: 1;
}

nav ul li a {
    display: block;
    padding: 0 10px;
    color: black;
    font-size: 20px;
    text-decoration: none;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    line-height: 60px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

nav ul li a i {
    padding-right: 20px;
    font-size: 26px;
}

nav ul li a.active {
    color: #8a6501;
    text-decoration: underline;
}

nav ul li a:not(.active):hover {
    color: #b58502;
}

@media (min-width: 1600px) {
    nav {
        left: calc((100vw - 1600px) / 2);
    }
}

@media (max-width: 1279px) {
    nav {
        padding: 0;
    }
}

@media (max-width: 1023px) {
    nav {
        height: 56px
    }
    
    nav ul li a {
        font-size: 16px;
        line-height: 56px;
    }

    nav ul li a i {
        font-size: 22px;
    }
}

@media (max-width: 767px) {
    nav {
        height: 54px
    }

    nav ul li a {
        font-size: 14px;
        line-height: 54px;
    }

    nav ul li a i {
        padding-right: 10px;
        font-size: 20px;
    }
}

@media (max-width: 639px) {
    nav {
        padding: 0 5%;
    }

    nav ul li a span {
        display: none;
    }

    nav ul li a i {
        font-size: 24px;
        vertical-align: middle;
    }
}

footer {
    padding: 10px 0;
    background-color: #bbb;
    text-align: center;
    font-size: 18px;
}

footer span {
    font-style: italic;
}

@media (max-width: 767px) {
    footer {
        font-size: 14px;
    }
}

@media (max-width: 359px) {
    footer {
        font-size: 12px;
    }
}
